<template>
  <div class="card">
    <div class="card-body">
      <h4 class="card-title">Add New Account</h4>
      <div class="forms-sample">
        <div class="form-group">
          <label for="">Name</label>
          <input v-model="name" type="text" class="form-control" placeholder="Name">
        </div> 
        <div class="form-group">
          <label for="">Email</label>
          <input v-model="email" type="email" class="form-control" placeholder="Email">
        </div>   
        <div class="form-group">
          <label for="">Password</label>
          <input v-model="password" type="password" class="form-control" placeholder="*******">
        </div>               
        <div class="form-group">
          <label for="" class="col-12 pl-0">Role</label>
          <select v-model="role" class="form-control form-control-lg col-3">
            <option>HR</option>
            <option>Interviewer</option>
            <option>Admin</option>
          </select>
        </div>         
        <button @click="Submit" class="btn btn-success mt-2">Save</button>
      </div>
    </div>   
  </div> 
</template>
<script>
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
export default {
  name: 'AddAccountModal',
  data() {
    return {
      name: '',
      email: '',
      role: 'Admin',
      none: '',
      password: ''
    }
  },
  created: function()
  {
    /*axios.post(api.baseUrl + api.addAccount, {
      "name": "morpheus",
      "job": "leader"
    }).then(res => this.accountlist = res.data)
      .catch(err => alert (err));*/
  },
  methods: {
    Submit(){
      let Data = {
        "userName": this.email,
        "fullName": this.name,
        "passwordHash": this.password,
        "role": this.role
      };

      const $vm = this;
      axios.post(api.baseUrl + api.addAccount, 
        Data, // the data to post
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        }).then(res => {
          this.$modal.hide('AddAccountModal');
          $vm.$emit('searchAccounts');
          this.$toast.open({
              message: "Create new account successful",
              type: 'success',
              position: 'top-right',
              dismissible: true   
          })            
          this.none = res
        })
        .catch(err => {
          this.$toast.open({
              message: "Please enter valid all fields",
              type: 'success',
              position: 'top-right',
              dismissible: true   
          })            
          this.none = err
        })
      ;  
    }
  }
}
</script>

