<template>
  <div class="card">
    <div class="card-body">
      <h4 class="card-title">Edit Account</h4>
      <div class="forms-sample">
        <div class="form-group">
          <label for="">Name</label>
          <input id="name" v-model="(this.$store.getters.DetailAccountId).fullName" type="text" class="form-control" placeholder="Name">
        </div> 
        <div class="form-group">
          <label for="">Email</label>
          <input id="email" v-model="(this.$store.getters.DetailAccountId).userName" type="email" class="form-control" placeholder="Email">
        </div>                 
        <div class="form-group">
          <label for="" class="col-12 pl-0">Role</label>
          <select id="role" v-model="(this.$store.getters.DetailAccountId).role" class="form-control form-control-lg col-3">
            <option value="HR">HR</option>
            <option value="Interviewer">Interviewer</option>
            <option value="Admin">Admin</option>
          </select>
        </div>         
        <button @click="Update" class="btn btn-success mt-2">Save</button>
      </div>
    </div> 
    <modal name="Notice" @before-open="beforeOpen">
        <div class="p-4">{{ notice }}</div>
    </modal>            
  </div> 
</template>
<script>
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
import {store} from '../../store/store'
export default {
  name: 'EditAccountModal',
  store,
  data() {
    return {
      notice: '',
      none: ''
    }
  },
  created: function()
  {
    /*axios.post(api.baseUrl + api.addAccount, {
      "name": "morpheus",
      "job": "leader"
    }).then(res => this.accountlist = res.data)
      .catch(err => alert (err));*/
  },
  methods: {
    beforeOpen (event) {
      this.notice = event.params;
    },
    Update(){
      let Data = {
        "userName": document.getElementById('email').value,
        "fullName": document.getElementById('name').value,
        "role": document.getElementById('role').value,
        "id": (this.$store.getters.DetailAccountId).id
      }
      axios.put(api.baseUrl + api.UpdateAccount, 
        Data,
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        }).then(res => {
          this.$store.commit('RELOAD', Math.random() + res)
          this.$toast.open({
              message: "Update successfull",
              type: 'success',
              position: 'top-right',
              dismissible: true   
          })           
          this.none = res
        })
        .catch(err => {
          this.$toast.open({
              message: "Update unsuccessfull",
              type: 'error',
              position: 'top-right',
              dismissible: true   
          })           
          this.none = err
        }); 
    }
  }
}
</script>

