<template>
<div class="card">
    <div class="card-body">
        <h4 class="card-title">Report</h4>
        <div class="forms-sample row">
            <div class="form-group col-2">
              <label for="">From</label>
              <DateTimePicker v-model="dateFrom" name="uniquename" :config="{format: 'DD/MM/YYYY'}"></DateTimePicker>
            </div> 
            <div class="form-group col-2">
              <label for="">To</label>
              <DateTimePicker v-model="dateTo" name="uniquename" :config="{format: 'DD/MM/YYYY'}"></DateTimePicker>
            </div> 
            <div class="form-group col-8">
              <button @click="search" class="btn btn-success mt-4">Search</button><button @click="download" class="btn btn-success mt-4 ml-3">Download</button>
            </div>        
            
        </div>
    </div>
    <div class="row">
      <div class="col-md-12" v-if='ShowChartAndTable'>
        <div id="chartDiv" style="width: 100%; height: 400px;"></div>
      </div>      
      <div class="w-100 text-center my-4">
          <label class="h2" style="color: rgb(64, 96, 164)">Candidate Statistic Report</label>
      </div>
      <div class="col-md-12" v-if='ShowChartAndTable'>
        <table class="table mt-3 mb-5">
          <thead>
            <tr>
              <th class="text-center">#</th>
              <th class="text-center">Range Time</th>
              <th class="text-center">Candidate Added</th>
              <th class="text-center">Candidate Contacted</th>
              <th class="text-center">Candidate Interviewed</th>
              <th class="text-center">Candidate Offered</th>
              <th class="text-center">Candidate Hired</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(data, index) in DataReport" :key="data.time" >
              <td class="text-center">{{ index + 1 }}</td>
              <td class="text-center">{{ data.time }}</td>
              <td class="text-center">{{ data.candidateAdded }}</td>
              <td class="text-center">{{ data.candidateContacted }}</td>
              <td class="text-center">{{ data.candidateInterviewed }}</td>
              <td class="text-center">{{ data.candidateOffered }}</td>
              <td class="text-center">{{ data.candidateHired }}</td> 
            </tr>
          </tbody>
        </table>        
      </div>
    </div>
</div>
</template>
<script>
import axios from 'axios';
import api from '../../api'
import DateTimePicker from 'vue-bootstrap-datetimepicker';
import * as am4core from '@amcharts/amcharts4/core'
import * as am4charts from '@amcharts/amcharts4/charts'
import Am4themesAnimated from '@amcharts/amcharts4/themes/animated'
am4core.useTheme(Am4themesAnimated)
export default {
  data() {
    return {
      msg: "Result Report",
      dateFrom: '01/01/2019',
      dateTo: '01/05/2019',
      DataReport : null,
      DataChart : [],
      ShowChartAndTable: false
    };
  },
  mounted () {
  },
  components: {
    DateTimePicker
  },
  created: function () {
    this.search()
  },
  methods: {
    download() {
      window.location.href =
        api.Report + "exportReport?fromDate="+this.dateFrom+"&toDate="+this.dateTo;
    },
    search() {
      this.$set(this, 'ShowChartAndTable', true)
      axios.get(api.Report + "data?fromDate="+this.dateFrom+"&toDate="+this.dateTo
      ).then(res => {
        this.$set(this, 'DataReport', res.data)
        let PushData = []
        res.data.forEach(Item => {
          let SubDate = (Item.time.split(' - '))[0]
          SubDate = SubDate.split('/')
          PushData.push({category: SubDate[1] + '/' + SubDate[2],value1: Item.candidateAdded, value2: Item.candidateContacted, value3: Item.candidateInterviewed, value4: Item.candidateOffered, value5: Item.candidateHired})
        })
        let chart = am4core.create('chartDiv', am4charts.XYChart)
        chart.hiddenState.properties.opacity = 0
        chart.data = PushData
        chart.colors.step = 3
        chart.padding(30, 30, 10, 30)        
        chart.numberFormatter.numberFormat = '#a'
        chart.legend = new am4charts.Legend()
        chart.legend.useDefaultMarker = true
        chart.legend.itemContainers.template.togglable = false
        chart.legend.itemContainers.template.focusable = false
        let markerTemplate = chart.legend.markers.template
        let marker = chart.legend.markers.template.children.getIndex(0)
        marker.cornerRadius(0, 0, 0, 0)
        marker.stroke = am4core.color('#0672FF')
        markerTemplate.width = 25
        markerTemplate.height = 7
        let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis())
        categoryAxis.dataFields.category = 'category'
        categoryAxis.renderer.grid.template.disabled = true
        categoryAxis.renderer.minGridDistance = chart.data.length
        let valueAxis = chart.yAxes.push(new am4charts.ValueAxis())
        valueAxis.min = 0
        valueAxis.calculateTotals = true
        valueAxis.renderer.minGridDistance = 50
        let series1 = chart.series.push(new am4charts.ColumnSeries())
        series1.columns.template.width = am4core.percent(50)
        series1.tooltip.getFillFromObject = false
        series1.tooltip.background.fill = am4core.color('#fff')
        series1.tooltip.label.fill = am4core.color('#000')
        series1.columns.template.tooltipPosition = 'pointer'
        series1.stroke = am4core.color('#5a9bd5')
        series1.fill = am4core.color('#5a9bd5')
        series1.tooltip.background.cornerRadius = 0
        series1.columns.template.tooltipHTML = `        
      <div class="title-left">candidate Added - {category}</div>
        <table>
            <tr>
                <th align="left" class="tooltip-padding-div-legend">
                    <div class="div-legend-0"></div>
                </th>
                <td align="left" class="tooltip-padding-name">Candidate Added</td>
                <td align="right">{value1}</td>
            </tr>                                     
        </table>`
        series1.name = 'Candidate Added'
        series1.dataFields.categoryX = 'category'
        series1.dataFields.valueY = 'value1'
        series1.dataItems.template.locations.categoryX = 0.5
        series1.stacked = true
        let bullet1 = series1.bullets.push(new am4charts.LabelBullet())
        bullet1.interactionsEnabled = false

        let series2 = chart.series.push(new am4charts.ColumnSeries())
        series2.columns.template.width = am4core.percent(50)
        series2.tooltip.getFillFromObject = false
        series2.tooltip.background.fill = am4core.color('#fff')
        series2.tooltip.label.fill = am4core.color('#000')
        series2.columns.template.tooltipPosition = 'pointer'
        series2.stroke = am4core.color('#ed7d31')
        series2.fill = am4core.color('#ed7d31')
        series2.tooltip.background.cornerRadius = 0
        series2.columns.template.tooltipHTML =
        `        
      <div class="title-left">Candidate Contacted - {category}</div>
        <table>
            <tr>
                <th align="left" class="tooltip-padding-div-legend">
                    <div class="div-legend-1"></div>
                </th>
                <td align="left" class="tooltip-padding-name">Candidate Contacted</td>
                <td align="right">{value2}</td>
            </tr>                                   
        </table>`
        series2.name = 'Candidate Contacted'
        series2.dataFields.categoryX = 'category'
        series2.dataFields.valueY = 'value2'
        series2.dataItems.template.locations.categoryX = 0.5
        series2.stacked = true
        let bullet2 = series2.bullets.push(new am4charts.LabelBullet())
        bullet2.interactionsEnabled = false         

        let series3 = chart.series.push(new am4charts.ColumnSeries())
        series3.columns.template.width = am4core.percent(50)
        series3.tooltip.getFillFromObject = false
        series3.tooltip.background.fill = am4core.color('#fff')
        series3.tooltip.label.fill = am4core.color('#000')
        series3.columns.template.tooltipPosition = 'pointer'
        series3.stroke = am4core.color('#a5a5a5')
        series3.fill = am4core.color('#a5a5a5')
        series3.tooltip.background.cornerRadius = 0
        series3.columns.template.tooltipHTML =
        `        
      <div class="title-left">Candidate Interviewed - {category}</div>
        <table>
            <tr>
                <th align="left" class="tooltip-padding-div-legend">
                    <div class="div-legend-2"></div>
                </th>
                <td align="left" class="tooltip-padding-name">Candidate Interviewed</td>
                <td align="right">{value3}</td>
            </tr>                                           
        </table>`
        series3.name = 'Candidate Interviewed'
        series3.dataFields.categoryX = 'category'
        series3.dataFields.valueY = 'value3'
        series3.dataItems.template.locations.categoryX = 0.5
        series3.stacked = true
        let bullet3 = series3.bullets.push(new am4charts.LabelBullet())
        bullet3.interactionsEnabled = false

        let series4 = chart.series.push(new am4charts.ColumnSeries())
        series4.columns.template.width = am4core.percent(50)
        series4.tooltip.getFillFromObject = false
        series4.tooltip.background.fill = am4core.color('#fff')
        series4.tooltip.label.fill = am4core.color('#000')
        series4.columns.template.tooltipPosition = 'pointer'
        series4.stroke = am4core.color('#fec000')
        series4.fill = am4core.color('#fec000')
        series4.tooltip.background.cornerRadius = 0
        series4.columns.template.tooltipHTML =
        `        
      <div class="title-left">Candidate Offered - {category}</div>
        <table>
            <tr>
                <th align="left" class="tooltip-padding-div-legend">
                    <div class="div-legend-3"></div>
                </th>
                <td align="left" class="tooltip-padding-name">Candidate Offered</td>
                <td align="right">{value4}</td>
            </tr>                                     
        </table>`
        series4.name = 'Candidate Offered'
        series4.dataFields.categoryX = 'category'
        series4.dataFields.valueY = 'value4'
        series4.dataItems.template.locations.categoryX = 0.5
        series4.stacked = true
        let bullet4 = series4.bullets.push(new am4charts.LabelBullet())
        bullet4.interactionsEnabled = false

        let series5 = chart.series.push(new am4charts.ColumnSeries())
        series5.columns.template.width = am4core.percent(50)
        series5.tooltip.getFillFromObject = false
        series5.tooltip.background.fill = am4core.color('#fff')
        series5.tooltip.label.fill = am4core.color('#000')
        series5.tooltip.background.cornerRadius = 0
        series5.columns.template.tooltipPosition = 'pointer'
        series5.stroke = am4core.color('#4471c4')
        series5.fill = am4core.color('#4471c4')
        series5.columns.template.tooltipHTML =
        `        
      <div class="title-left">Candidate Hired - {category}</div>
        <table>   
            <tr>
                <th align="left" class="tooltip-padding-div-legend">
                    <div class="div-legend-4"></div>
                </th>
                <td align="left" class="tooltip-padding-name">Candidate Hired</td>
                <td align="right">{value5}</td>
            </tr>                                     
        </table>`
        series5.name = 'Candidate Hired'
        series5.dataFields.categoryX = 'category'
        series5.dataFields.valueY = 'value5'
        series5.stacked = true
        let bullet5 = series5.bullets.push(new am4charts.LabelBullet())
        bullet5.interactionsEnabled = false       
          })
        }
  }
}
</script>
<style>
.tooltip-padding-div-legend {
    padding-right: 5px !important;
}
.tooltip-padding-name {
    padding-right: 70px !important;
}
.title-left {
    font-weight: bold;
    text-align: left;
}
tspan {
  max-width: 50px!important
}  
</style>

