import Vue from 'vue';
import Vuex from 'vuex';
import api from '../api'
import axios from 'axios';
import Cookies from 'js-cookie'
Vue.use(Vuex);
 
export const store = new Vuex.Store({
  state: {
    token: null,
    curentTab: 1,
    editCv: null,
    Level: null,
    Position: null,
    CvList: null,
    DetailCvObj: {},
    NumberPageCv: 0,
    AccountList: null,
    RoundOfCv : null,
    idEditCv: null,
    LogicalTestObj: null,
    DetailAccountId: {},
    InterviewerList:[],
    selectedInterviewers:[],
    Reload: null,
    Reload_: null,
    RoundStatus: [{"name":"Not Process Yet","index":1,"statusOf":"CV"},{"name":"In Process","index":2,"statusOf":"CV"},{"name":"Hired","index":3,"statusOf":"CV"},{"name":"Archived","index":4,"statusOf":"CV"},{"name":"Not Invite Yet","index":1,"statusOf":"Test"},{"name":"Invited","index":2,"statusOf":"Test"},{"name":"Rejected","index":-2,"statusOf":"Test"},{"name":"Accepted","index":3,"statusOf":"Test"},{"name":"Not Come","index":-3,"statusOf":"Test"},{"name":"Tested","index":4,"statusOf":"Test"},{"name":"Pass","index":5,"statusOf":"Test"},{"name":"Not Pass","index":-4,"statusOf":"Test"},{"name":"Not Invite Yet","index":1,"statusOf":"Interview"},{"name":"Invited","index":2,"statusOf":"Interview"},{"name":"Rejected","index":-2,"statusOf":"Interview"},{"name":"Accepted","index":3,"statusOf":"Interview"},{"name":"Not Come","index":-3,"statusOf":"Interview"},{"name":"Interviewed","index":4,"statusOf":"Interview"},{"name":"Feedbacked","index":5,"statusOf":"Interview"},{"name":"Pass","index":6,"statusOf":"Interview"},{"name":"Not Pass","index":-5,"statusOf":"Interview"},{"name":"Not Offer Yet","index":1,"statusOf":"Offer"},{"name":"Offered","index":2,"statusOf":"Offer"},{"name":"Rejected","index":-2,"statusOf":"Offer"},{"name":"Accepted","index":3,"statusOf":"Offer"},{"name":"Not Come","index":-3,"statusOf":"Offer"},{"name":"Hired","index":4,"statusOf":"Offer"}]
  },
  getters: {
    Token: state => state.token,
    curentTab: state => state.curentTab,
    CvList: state => state.CvList,
    AccountList: state => state.AccountList,
    editCV: state => state.editCV,
    Level: state => state.Level,
    Position: state => state.Position,
    RoundStatus: state => state.RoundStatus,
    NumberPageCv: state => state.NumberPageCv,
    DetailCvObj: state => state.DetailCvObj,
    RoundOfCv: state => state.RoundOfCv,
    idEditCv: state => state.idEditCv,
    DetailAccountId: state => state.DetailAccountId,
    LogicalTestObj: state => state.LogicalTestObj,
    InterviewerList: state => state.InterviewerList,
    Reload: state => state.Reload,
    Reload_: state => state.Reload_,
    selectedInterviewers: state => state.selectedInterviewers,
  },  
  mutations: {
    CHANGE_TAB : (state, openTab) => {
      state.curentTab = openTab
    },
    EDIT_CV : (state, editCv) => {
      state.editCv = editCv
    } ,
    CHANGE_TOKEN : (state, token) => {
      state.token = token
    },
    CHANGE_LEVEL : (state, Level) => {
      state.Level = Level
    },
    CHANGE_POSITION : (state, Position) => {
      state.Position = Position
    },
    CHANGE_CV_LIST : (state, CvList) => {
      state.CvList = CvList
    },
    CHANGE_ACCOUNT_LIST : (state, AccountList) => {
      state.AccountList = AccountList
    },  
    CHANGE_ROUND_STATUS : (state, RoundStatus) => {
      state.RoundStatus = RoundStatus
    },
    CHANGE_CV_PAGE : (state, NumberPageCv) => {
      state.NumberPageCv = NumberPageCv
    },
    DETAIL_CV_OBJ : (state, DetailCvObj) => {
      state.DetailCvObj = DetailCvObj
    },
    ROUND_OF_CV : (state, RoundOfCv) => {
      state.RoundOfCv = RoundOfCv
    },
    ID_EDIT_CV : (state, idEditCv) => {
      state.idEditCv = idEditCv
    },
    CHANGE_ACCOUNT_ID : (state, DetailAccountId) => {
      state.DetailAccountId = DetailAccountId
    },
    LOGICAL_TEST_OBJ : (state, LogicalTestObj) => {
      state.LogicalTestObj = LogicalTestObj
    },
    GET_INTERVIEWERS : (state, InterviewerList) => {
      state.InterviewerList = InterviewerList
    },
    SET_SELECTED_INTERVIEWERS : (state, selectedInterviewers) => {
      state.selectedInterviewers = selectedInterviewers
    },
    RELOAD : (state, Reload) => {
      state.Reload = Reload
      state.Reload_ = Reload
    }
  },
  actions: {
    GET_CV_DETAIL : async (context, id) => {
      context.commit('ID_EDIT_CV', id)
      let { data } = await axios.get(api.baseUrl + api.getDetailCv + id, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      })
      function parseIsoDatetime(dtstr) {
        var dt = dtstr.split(/[: T-]/).map(parseFloat);
        return new Date(dt[0], dt[1] - 1, dt[2], dt[3] || 0, dt[4] || 0, dt[5] || 0, 0);
      }
      let req = data
      req.candidateDoB = parseIsoDatetime(req.candidateDoB)
      req.inComingDate = parseIsoDatetime(req.inComingDate)
      context.commit('DETAIL_CV_OBJ', req)
    },
    GET_CV_ROUND : async (context, id) => { 
      let { data } = await axios.get(api.baseUrl + api.RoundOfCv + id, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      })    
      let req = data
      function parseIsoDatetime(dtstr) {
        var dt = dtstr.split(/[: T-]/).map(parseFloat);
        return new Date(dt[0], dt[1] - 1, dt[2], dt[3] || 0, dt[4] || 0, dt[5] || 0, 0);
      }      
      req.forEach(Item => {
        Item.time = parseIsoDatetime(Item.time)         
      })
      context.commit('ROUND_OF_CV', req)
      context.commit('CHANGE_TAB', 3)
      context.commit('RELOAD', Math.random())
    },
    SET_CV_ROUND : async (context, RoundOfCv) => { 
      context.commit('ROUND_OF_CV', [RoundOfCv])
      context.commit('CHANGE_TAB', 3)
    }
  },
  modules: {
  }
});
