<template>
  <div class="card mt-z">
    <div class="card-body">
      <h4 class="card-title">Add New CV</h4>
      <div class="forms-sample">
        <div class="form-group">
          <label for="">Candidate</label>
          <input v-model="name" type="text" class="form-control" placeholder="">
        </div> 
        <div class="form-group">
          <label for="">DoB</label>
          <DateTimePicker v-model="Dob" name="uniquename" :config="{format: 'MM/DD/YYYY'}"></DateTimePicker>
        </div>        
        <div class="form-group row">
          <label for="" class="col-12">Apply Position</label>
          <div class="col-3">
            <select v-model="level" class="form-control form-control-lg">
              <option v-for="(level) in (this.$store.getters.Level)" :key="level.id">{{ level['name'] }}</option>
            </select>
          </div>
          <div class="col-3">
            <select v-model="position" class="form-control form-control-lg  mr-3">
              <option v-for="(position) in (this.$store.getters.Position)" :key="position.id">{{ position['name'] }}</option>
            </select>
          </div>
          <div class="col-6">
            <input v-model="note" type="text" class="form-control form-control-lg" placeholder="Note">
          </div>
        </div> 
        <div class="row">
          
          <div class="form-group col-6">
            <label for="">Salary Expect</label>
            <money v-model="salaryExpect" v-bind="money" class="form-control"></money>
          </div> 
          <!--<div class="form-group col-6">
            <label for="">Salary Offer</label>
            <money v-model="salaryOffer" v-bind="money" class="form-control"></money>
          </div>  -->
        </div>                      
        <div class="form-group mt-4 row"> 
          <div class="button-wrapper col-4 ml-1">
            <span class="label">
              Upload File
            </span>
            <input @change="changeFile" ref="file" type="file" name="upload" id="upload" class="upload-box" placeholder="Upload File">
          </div>
          <input type="" name="" class="col-7 ml-3 detail-file-input" disabled="" v-model="filepath">
        </div>         
        <button @click="Submit" class="btn btn-success mt-2">Save</button>
      </div>
    </div>        
  </div> 
</template>
<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css'    
import DateTimePicker from 'vue-bootstrap-datetimepicker';
import {store} from '../../store/store'
import api from '../../api'
import {Money} from 'v-money'
export default {
  name: 'CvModal',
  store,
  data() {
    return {
      filepath: 'No choose file',
      fileUpload: '',
      name: '',
      Dob: '01/01/1999',
      level: 'Intern',
      position: '.NET',
      note: '',
      status: 'Apply',
      salaryExpect: 0,
      salaryOffer: 0,
      money: {
          decimal: ',',
          thousands: '.',
          prefix: '',
          suffix: ' đ',
          precision: 0,
          masked: false
        }
    }
  },
  components: {
    DateTimePicker,
    Money
  },
  created: function()
  {
    if (!this.$store.getters.Level){
      axios.get(api.baseUrl + api.getLevel, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_LEVEL', res.data))
        .catch(err => alert (err));
    }  
    if (!this.$store.getters.Position){
      axios.get(api.baseUrl + api.getPosition, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_POSITION', res.data))
        .catch(err => alert (err));
    }    
  },
  methods: {
    changeFile(event) {
      this.fileUpload = event.target.files[0];
      this.filepath = document.getElementById('upload').files[0].name
    },
    Submit(){
      let levelId = (this.$store.getters.Level).find(Obj => Obj.name === this.level);
      let positionId = (this.$store.getters.Position).find(Obj => Obj.name === this.position);
      let formData = new FormData();
      formData.append('file', this.fileUpload);
      axios.post(api.baseUrl + api.upload,
        formData,
        {
          headers: {
              "Authorization" : "Bearer " + Cookies.get('token')
          }
        }
      ).then(response => {
        let Data = {
          "candidateName": this.name,
          "levelId": Number(levelId.id),
          "candidateDoB": (new Date(this.Dob + ' 12:00:00')).toISOString(),
          "positionId": Number(positionId.id),
          "applyPositionNote": this.note,
          "path": response.data,
          "salaryExpect": Number(this.salaryExpect),
          "salaryOffer": Number(this.salaryOffer),
        };
        axios.post(api.baseUrl + api.addCv, 
          Data,
          { headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
            }/* eslint-disable no-unused-vars */
          }).then(res => {
            this.$store.commit('CHANGE_TAB', 1)
            this.$modal.hide('AddCvModal');
            location.reload();
          })
          .catch(err => {
            alert('Error');
          });    
      })
      .catch(err => {
        this.$toast.open({
            message: "Please upload 1 Cv file",
            type: 'error',
            position: 'top-right',
            dismissible: true   
        })        
      });
    }
  }
}
</script>

<style>

.button-wrapper {
  position: relative;
  text-align: center;
}

.button-wrapper span.label {
  position: relative;
  z-index: 0;
  display: inline-block;
  width: 100%;
  background: #ff6258;
  cursor: pointer;
  color: #fff;
  padding: 10px 0;
  text-transform:uppercase;
  font-size:12px;
}

#upload {
    display: inline-block;
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 50px;
    top: 0;
    left: 0;
    opacity: 0;
    cursor: pointer;
}
.detail-file-input {
  background:none;
  border:none;
}
.date-picker input{
  height: 30px;
  border: none;
  width: 100%;   
  z-index: 100; 
}
.v--modal-box {
  top: 200px!important;
}
</style>
