<template>
  <div id="app">
      <nav class="navbar default-layout col-lg-12 col-12 p-0 fixed-top d-flex flex-row">
        <div class="text-center navbar-brand-wrapper d-flex align-items-top justify-content-center nav-logo " style="background:#4160a4">
          <a class="navbar-brand brand-logo mt-3">
            <img src="https://gemvietnam.com/wp-content/uploads/2018/05/logo.png" alt="logo" style="width: 90%" /> </a>
          <a class="navbar-brand brand-logo-mini">
            <img src="https://gemvietnam.com/wp-content/uploads/2018/08/favicon_gem.png" alt="logo"  style="width: 90%"/> </a>
        </div>
        <div class="navbar-menu-wrapper d-flex align-items-center nav-bar">
          <ul class="navbar-nav w-100 pr-3">
            <li class="nav-item font-weight-semibold d-none d-lg-block h2">Recruitment Management Tool</li>
            <li class="nav-item ml-auto">
              <a class="nav-link dropdown-toggle d-block" style="z-index: 9999" id="UserDropdown" @click="ChangePassword=!ChangePassword">
                <img class="img-xs rounded-circle" src="https://img.icons8.com/bubbles/2x/admin-settings-male.png" alt="Profile image"> 
              </a>            
              <div v-if="ChangePassword" class="dropdown-menu dropdown-menu-right navbar-dropdown d-block user-dropdown" aria-labelledby="UserDropdown">
                <div class="dropdown-header text-center">
                  <img class="img-md rounded-circle" src="https://img.icons8.com/bubbles/2x/admin-settings-male.png" alt="Profile image">
                  <p class="mb-1 mt-3 font-weight-semibold">{{ fullName }}</p>
                  <p class="font-weight-light text-muted mb-0">{{ userName }}</p>
                  <label class="badge mt-1" v-bind:class="role">{{ role }}</label>
                  <hr>
                  <p class="mb-1 mt-3 font-weight-semibold">Change Password</p>
                  <form v-on:submit.prevent="UpdatePassword">
                    <input v-model="NewPassword" type="search" placeholder="New Password" class="form-control file-upload-info text-center">
                  </form>
                </div>
              </div>
          </li>        
          </ul>
        </div>
      </nav>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import axios from 'axios';
import api from '../api'
export default {
  name: 'app',
  data () {
    return {
      ChangePassword: false,
      fullName: Cookies.get('fullName'),
      userName: Cookies.get('userName'),
      role: Cookies.get('role'),
      NewPassword: null,
      id: Cookies.get('id'),
      none: null
    }
  },
  components: {

  },
  methods: {
    UpdatePassword : function() {
      let Data = {
        "userName": this.userName,
        "fullName": this.fullName,
        "role": this.role,
        "id": this.id,
        "passwordHash": this.NewPassword
      }
      axios.put(api.baseUrl + api.UpdateAccount, 
        Data,
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        }).then(res => {
          this.$toast.open({
              message: "Update successfull",
              type: 'success',
              position: 'top-right',
              dismissible: true   
          })           
          this.none = res
        })
        .catch(err => {
          this.$toast.open({
              message: "Update unsuccessfull",
              type: 'error',
              position: 'top-right',
              dismissible: true   
          })           
          this.none = err
        });       
    }
  }
}
</script>

