<template>
  <div class="">
    <div class="content-wrapper">
      <div class="row">
        <div class="col-lg-12 grid-margin stretch-card">
          <div class="card">
            <div class="card-body">
              <h4 class="card-title">Candidate Tracking File</h4>
              <div class="row mt-4">
                <CvFilter/>
                <table class="table mt-3">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Candidate</th>
                      <th>Position</th>
                      <th>Incoming Date</th>
                      <th>Status</th>
                      <th>Detail Process</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(cv, index) in (this.$store.getters.CvList)" :key="cv.id" >
                      <td>{{ index + 1 }}</td>
                      <td>{{ cv.candidateName }}</td>
                      <td>{{ cv.level.name }} {{ cv.position.name }} {{ cv.position.note }}</td>
                      <td>{{ cv.inComingDate | moment("DD/MM/YYYY") }}</td>
                      <td>
                        <label class="badge" :class="cv.status">{{ cv.status }}</label>
                      </td>
                      <td>
                        <a @click="EditCv(cv.id)"><i class="fa fa-edit"></i></a>
                        <a v-if="role!='Interviewer'" class="ml-2" v-confirm="{
                          loader: true,
                          message: 'Do you sure want delete?'}"
                          @click="DeleteCv(cv.id)">
                          <i class="fa fa-close text-danger"></i>
                        </a>
                      </td> 
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import CvFilter from '../filter/CvFilter.vue';   
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
import {store} from '../../store/store'
export default {
  name: 'AccountList',
  store,
  data() {
    return {
      accountlist: [],
      none: null,
      role: Cookies.get('role')
    }
  },
  components: {
    CvFilter
  },
  methods: {
    EditCv (id) {
      this.$store.dispatch('GET_CV_DETAIL', id)
      this.$store.dispatch('GET_CV_ROUND', id)
    },
    DeleteCv (id) {
      axios.delete(api.baseUrl + api.DeleteCv + id,
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        }).then(res =>{
            location.reload()
            this.none = res
          }
        )
      .catch(err => {
        this.$toast.open({
            message: "Can't delete with your Account",
            type: 'error',
            position: 'top-right',
            dismissible: true   
        })  
        this.none = err
      });
    }    
  },
  created: function () {
    let Data = {
      "levelId": 0,
      "positionId": 0,
      "currentPage": 1,
      "keyword": '',
      "pageSize": 10
    }
    axios.post(api.baseUrl + api.cvList, 
      Data,
      {
      headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
      }
    }).then(res => {
        let req = res.data
        req.pageData.forEach(item => {
          item.candidateDoB = (new Date(item.candidateDoB)).toString()
          item.inComingDate = (new Date(item.inComingDate)).toString()
        })
      this.$store.commit('CHANGE_CV_LIST', req.pageData)
      this.$store.commit('CHANGE_CV_PAGE', req.totalPage)
    })
      .catch(err => alert (err));
  }
}
</script>

