<template>
  <div class="col-md-6 grid-margin stretch-card">
      <div class="card">
          <div class="card-body">
              <div class="d-flex justify-content-between pb-3">
                  <h4 class="card-title mb-0">Process</h4>
              </div>
              <div>
              <div class="md-stepper-horizontal orange">
                <div class="md-step">
                  <div class="md-step-circle Pass"><span><i class=" fa fa-binoculars"></i></span></div>
                  <div class="md-step-title">Acquisition</div>
                  <div class="md-step-bar-left"></div>
                  <div class="md-step-bar-right"></div>
                </div>
                <div v-if="GetProcess.length == 0 || GetProcess[0].name == 'Test'" class="md-step">
                  <div class="md-step-circle" :class='Test'><span><i class="  fa fa-puzzle-piece"></i></span></div>
                  <div class="md-step-title">Logical Test</div>
                  <div class="md-step-optional">{{ Test }}</div>
                  <div class="md-step-bar-left"></div>
                  <div class="md-step-bar-right"></div>
                </div>
                <div class="md-step">
                  <div class="md-step-circle" :class='Interview'><span><i class="  fa fa-users "></i></span></div>
                  <div class="md-step-title">Interview</div>
                  <div class="md-step-optional">{{ Interview }}</div>
                  <div class="md-step-bar-left"></div>
                  <div class="md-step-bar-right"></div>
                </div>
                <div class="md-step">
                  <div class="md-step-circle" :class='Offer'><span><i class="fa fa-handshake-o"></i></span></div>
                  <div class="md-step-title">Offer</div>
                  <div class="md-step-optional">{{ Offer }}</div>
                  <div class="md-step-bar-left"></div>
                  <div class="md-step-bar-right"></div>
                </div>
              </div>                
              </div>
              <ul class="timeline mt-5">
                  <GoToRound v-if="(this.$store.getters.RoundOfCv).length == 0 && role != 'Interviewer'" />
                  <LogicalTest v-if="this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )"/>
                  <Interview v-if="this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )"/>
                  <Offer v-if="this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Offer' ) && role != 'Interviewer'"/>
              </ul>
          </div>
      </div>
  </div>     
</template>
<script>
import GoToRound from './process-cv/GoToRound.vue';  
import LogicalTest from './process-cv/LogicalTest.vue';  
import Interview from './process-cv/Interview.vue';
import Offer from './process-cv/Offer.vue';
import {store} from '../../store/store'  
import Cookies from 'js-cookie'
export default {
  name: 'EditCv',
  store,
  data() {
    return {
      role: Cookies.get('role'),
      isActive: false,
      Test: null,
      Interview: null,
      Offer: null,
      GetProcess: null
    }
  },
  components: {
    GoToRound,
    LogicalTest,
    Interview,
    Offer
  },
  created: function() {
    this.$set(this, 'GetProcess', this.$store.getters.RoundOfCv)
    let CheckTest = this.GetProcess.find( ({ name }) => name === `Test` )
    let CheckInterview = this.GetProcess.find( ({ name }) => name === `Interview`  )
    let CheckOffer = this.GetProcess.find( ({ name }) => name === `Offer` )
    if (CheckTest) this.$set(this, 'Test', CheckTest.status); else this.$set(this, 'Test', 'Yet')
    if (CheckInterview) this.$set(this, 'Interview', CheckInterview.status); else this.$set(this, 'Interview', 'Yet')
    if (CheckOffer) this.$set(this, 'Offer', CheckOffer.status); else this.$set(this, 'Offer', 'Yet')
  }
}
</script>

