<template>
  <div class="content-wrapper">
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h4 class="card-title">Account Manager</h4>
            <div class="row mt-4">
              <AccountFilter/>  
              <table class="table mt-3">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Account Name</th>
                    <th>Email</th>
                    <th>Create Date</th>
                    <th>Role</th>
                    <th>Detail</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(account, index) in this.$store.getters.AccountList" :key="account.id">
                    <td>{{ index + 1 }}</td>
                    <td>{{ account.fullName }}</td>
                    <td>{{ account.userName }}</td>
                    <td>{{ account.createAt | moment("DD/MM/YYYY") }}</td>
                    <td>
                      <label class="badge" v-bind:class="account.role">{{ account.role }}</label>
                    </td>
                    <td>
                      <a @click="Edit(account.id); openModal()"><i class="text-primary fa fa-edit"></i></a>
                      <a v-confirm="{
                          loader: true,
                          message: 'Do you sure want delete?'}"
                          @click="Delete(account.id)"><i class="ml-1 text-danger fa fa-close"></i></a>
                    </td> 
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <modal name="EditAccount" >
      <EditAccount/>
    </modal> 
  </div>
</template>
<script>
import AccountFilter from '../filter/AccountFilter.vue';  
import EditAccount from '../modal/EditAccountModal.vue';  
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
import {store} from '../../store/store'
export default {
  name: 'AccountList',
  store,
  created: function () {
    if (!this.$store.getters.AccountList){
      let Data = {
        "currentPage": 1,
        "keyword": '',
        "pageSize": 1000
      }
      axios.post(api.baseUrl + api.accountList, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_ACCOUNT_LIST', res.data))
        .catch(err => alert (err));
    }   
  },
  methods: {
    openModal () {
      this.$modal.show('EditAccount');
    },
    closeModal () {
      this.$modal.hide('EditAccount');
    },
    Delete (id) {
      axios.delete(api.baseUrl + api.DeleteAccount + id,
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        })/* eslint-disable no-unused-vars */.then(res => {
          let Data = {
            "currentPage": 1,
            "keyword": '',
            "pageSize": 12
          }          
          axios.post(api.baseUrl + api.accountList, 
            Data,
            {
            headers: {
                "Authorization" : "Bearer " + Cookies.get('token')
            }
          }).then(res => {
            this.$store.commit('CHANGE_ACCOUNT_LIST', res.data)
            this.$toast.open({
                message: "Delete successful",
                type: 'success',
                position: 'top-right',
                dismissible: true   
            })              
          })
            .catch(err => alert (err));
        }) 
    },
    Edit (id) {
      axios.get(api.baseUrl + api.GetAcount + id,
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        }).then(res => {
          this.$store.commit('CHANGE_ACCOUNT_ID', res.data)
        })
    }
  },
  data() {
    return {
      accountlist: []
    }
  },
  components: {
    AccountFilter,
    EditAccount
  }
}
</script>

