<template>
    <div class="col-md-6 grid-margin stretch-card">
        <div class="card">
            <div class="card-body">
                <h4 class="card-title">Edit CV</h4>
                <div class="forms-sample">
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">Full Name</label>
                        <div class="col-sm-9">
                            <input :disabled="role == 'Interviewer'" id="candidateName" type="text" class="form-control" placeholder="" v-model="this.$store.getters.DetailCvObj.candidateName">
                        </div>
                    </div>
                    <div class="form-group row">
                      <label for="" class="col-12 col-form-label">Apply Position</label>
                        <div class="col-sm-3">
                            <select :disabled="role == 'Interviewer'" v-model="this.$store.getters.DetailCvObj.levelId" id="level" class="form-control form-control-lg">
                                <option v-for="(level) in (this.$store.getters.Level)" :key="level.id" v-bind:value="level.id">{{ level['name'] }}</option>
                              </select>
                        </div>
                        <div class="col-sm-3">
                          <select :disabled="role == 'Interviewer'" v-model="this.$store.getters.DetailCvObj.positionId" id="position" class="form-control form-control-lg">
                            <option v-for="(position) in (this.$store.getters.Position)" :key="position.id" v-bind:value="position.id">{{ position['name'] }}</option>
                          </select>
                        </div>
                        <div class="col-sm-6">
                          <input :disabled="role == 'Interviewer'" id="applyPositionNote" v-model="this.$store.getters.DetailCvObj.applyPositionNote" type="text" class="form-control form-control-lg" placeholder="Note">
                        </div>
                    </div> 
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">Incoming Date</label>
                        <div class="col-sm-9">
                            <DateTimePicker :disabled="role == 'Interviewer'" id="inComingDate" v-model="this.$store.getters.DetailCvObj.inComingDate" :config="{format: 'MM/DD/YYYY'}"></DateTimePicker>
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">Gender</label>
                        <div class="col-sm-9">
                            <select :disabled="role == 'Interviewer'" id="gender" v-model="this.$store.getters.DetailCvObj.gender" class="form-control">
                                <option>Male</option>
                                <option>Female</option>
                            </select>
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">Date Of Birth</label>
                        <div class="col-sm-9">
                            <DateTimePicker :disabled="role == 'Interviewer'" id="candidateDoB" v-model="this.$store.getters.DetailCvObj.candidateDoB" :config="{format: 'MM/DD/YYYY'}" ></DateTimePicker>
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">University</label>
                        <div class="col-sm-9">
                            <input :disabled="role == 'Interviewer'" id="university" v-model="this.$store.getters.DetailCvObj.university" type="text" class="form-control" placeholder="">
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">Address</label>
                        <div class="col-sm-9">
                            <input :disabled="role == 'Interviewer'" id="address" v-model="this.$store.getters.DetailCvObj.address" type="text" class="form-control" placeholder="">
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">CV Source</label>
                        <div class="input-group col-sm-9">
                            <input :disabled="role == 'Interviewer'" id="cvSource" v-model="this.$store.getters.DetailCvObj.cvSource" type="text" class="form-control" placeholder="">
                            <div class="input-group-append bg-primary border-primary">
                              <span class="input-group-text bg-transparent">
                                <a v-bind:href="this.$store.getters.DetailCvObj.path" class="text-light" target="_blank"><i class=" fa fa-paperclip text-light"></i></a>
                              </span>
                            </div>
                          </div>                        
                    </div>
                    <!--<div class="form-group row">
                        <label class="col-sm-3 col-form-label">Offer Salary</label>
                        <div class="col-sm-9">
                            <input id="offer" v-model="this.$store.getters.DetailCvObj.salaryOffer" type="number" class="form-control" placeholder="">
                        </div>
                    </div> --> 
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label">Expect Salary</label>
                        <div class="col-sm-9">
                            <input :disabled="role == 'Interviewer'" id="expect" v-model="this.$store.getters.DetailCvObj.salaryExpect" type="number" class="form-control" placeholder="">
                        </div>
                    </div>                                       
                    <div class="form-group row">
                        <label class="col-sm-3 col-form-label" for="">Note</label>
                        <div class="input-group col-sm-9">
                           <textarea :disabled="role == 'Interviewer'" id="note" v-model="this.$store.getters.DetailCvObj.note" class="form-control" rows="2"></textarea>
                        </div>
                    </div>
                    <button v-if="role!='Interviewer'" @click="Save" type="submit" class="btn btn-success mr-2">Save</button>
                </div>
            </div>
        </div>        
    </div>
</template>
<script>
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css'    
import DateTimePicker from 'vue-bootstrap-datetimepicker';
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
export default {
  name: 'InfoCv',
  data() {
    return {
      filepath: 'No choose file',
      fileUpload: '',
      date: '03/03/2019',
      id: 1,
      level: 'Intern',
      position: '.NET', 
      gender: 'Male',
      role: Cookies.get('role') 
    }
  },
  created: function(){
    if (!this.$store.getters.Level){
      axios.get(api.baseUrl + api.getLevel, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_LEVEL', res.data))
        .catch(err => alert (err));
    }  
    if (!this.$store.getters.Position){
      axios.get(api.baseUrl + api.getPosition, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_POSITION', res.data))
        .catch(err => alert (err));
    }  
    this.$store.commit('GET_CV_DETAIL', this.$store.getters.DetailCvObj.id)   
  },
  components: {
    DateTimePicker
  },
  methods: {
    Save() { 
      let inComingDate = new Date(document.getElementById("inComingDate").value + ' 12:00:00');
      let getcandidateDoB = new Date(document.getElementById("candidateDoB").value + ' 12:00:00'); 
      let Data = {
        "candidateName": document.getElementById("candidateName").value,
        "candidateDoB": getcandidateDoB.toISOString(),
        "gender": document.getElementById("gender").value,
        "levelId": Number(document.getElementById("level").value),
        "positionId": Number(document.getElementById("position").value),
        "university": document.getElementById("university").value,
        "address": document.getElementById("address").value,
        "note": document.getElementById("note").value,
        "applyPositionNote": document.getElementById("applyPositionNote").value,
        "status": this.$store.getters.DetailCvObj.status,
        "cvSource": document.getElementById("cvSource").value,
        "salaryExpect": Number(document.getElementById("expect").value),
        //"salaryOffer": Number(document.getElementById("offer").value),
        "inComingDate": inComingDate.toISOString(),
        "id": this.$store.getters.DetailCvObj.id
      }
      axios.put(api.baseUrl + api.cvUpdate, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }/* eslint-disable no-unused-vars */
      }).then(res => 
        //this.$modal.show('Alert', {note: 'Update successful', class: 'fa fa-check-circle text-success'})
        this.$toast.open({
            message: 'Update successful',
            type: 'success',
            position: 'top-right',
            dismissible: true   
        })
      )
        .catch(err =>         
            this.$toast.open({
                message: 'Update unsuccessful',
                type: 'danger',
                position: 'top-right',
                dismissible: true   
            })
        );      
    }
  }  
}
</script>

<style>
.detail-file-input {
  background:none;
  border:none;
} 
.date-picker input{
  height: 30px;
  border: none;
  width: 100%;    
}
</style>
