<template>
  <li class="timeline-item">
      <p class="timeline-content"><label class="col-sm-3">Acquisition</label></p>
      <p class="event-time">
      </p>
      <div class="card mt-3">
          <div class="card-body ">
              <div class="">
                  <button @click="GoTo('Test', 'Not Invite Yet')" type="button" class="btn btn-success btn-fw mr-2 mt-2"><i class="mdi mdi-check"></i>Create Logical Test</button>
                  <button @click="GoTo('Interview', 'Not Invite Yet')" type="button" class="btn btn-success btn-fw mr-2 mt-2"><i class="mdi mdi-check"></i>Create Interview</button>
                  <button @click="Archived" type="button" class="btn btn-danger btn-fw mt-2"><i class="mdi mdi-alert-outline"></i>Archive</button>                 
              </div>
          </div>

      </div>
  </li>
</template>
<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import {store} from '../../../store/store'
import api from '../../../api'
export default {
  name: 'GoToRound',
  data() {
    return {

    }
  },
  store,
  components: {

  },
  methods: {
    GoTo (name, status) {
      let Data = {
        "name": name,
        "cvId": this.$store.getters.idEditCv,
        "status": status
      }
      axios.post(api.baseUrl + api.AddRound, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }/* eslint-disable no-unused-vars */
      }).then(res => {
        Data = {
          "id": this.$store.getters.idEditCv,
          "status": "In Process"
        }
        axios.put(api.baseUrl + api.CvUpdateStatus, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
        }).then(res => {
          
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })        
        
      })    
    },
    Archived () {
      let Data = {
        "id": this.$store.getters.idEditCv,
        "status": "Archived"
      }
      axios.put(api.baseUrl + api.CvUpdateStatus, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }/* eslint-disable no-unused-vars */
      }).then(res => {
          this.$toast.open({
              message: "Archive successful",
              type: 'success',
              position: 'top-right',
              dismissible: true   
          }) 
      })      
    }
  }
}
</script>

