<template>
  <div class="w-100 overflow-auto">
    <CvList v-if = "this.$store.getters.curentTab == 1 "/>  
    <AccountList v-if = "this.$store.getters.curentTab == 2"/>
    <EditCv v-if = "this.$store.getters.curentTab == 3 "/>
    <Report v-if = "this.$store.getters.curentTab == 4 "/>
