      <div class="p-4"><i v-bind:class="vclass"></i> {{ notice }}</div>
    </modal>
  </div>
</template>
<script>
import AccountList from './body/AccountList.vue';
import CvList from './body/CvList.vue';
import EditCv from './body/EditCv.vue';
import Report from './body/Report.vue';
import {store} from '../store/store'
export default {
  name: 'maincontent',
  store,
  data() {
    return {
      currentTab: this.$store.getters.curentTab,
      notice: '',
      icon: '',
      vclass: ''
    };
  },
  methods: {
    beforeOpen (event) {
      this.notice = event.params.note;
      this.vclass = event.params.class;
    },
  },
  components: {
    AccountList,
    CvList,
    EditCv,
    Report
  }
}
</script>

