<template>
<div class="row login-temp">
    <form class="col-lg-3 col-md-6 mx-auto mt-5 pl-4 pr-4" v-on:submit.prevent="Login">
        <div class="push"></div>
        <div class="auto-form-wrapper mt-5">
            <div>
                <div class="form-group text-center">
                  <img src="https://gemvietnam.com/wp-content/uploads/2018/05/logo-big.png">
                </div>
                <div class="form-group mt-4">
                    <label class="label">Email</label>
                    <div class="input-group">
                        <input v-model="Username" type="text" class="form-control" placeholder="Enter your email">
                    </div>
                </div>
                <div class="form-group">
                    <label class="label">Password</label>
                    <div class="input-group">
                        <input id="password-field" v-model="Password" type="password" class="form-control" placeholder="*********">
                        <div class="input-group-append toggle-password" @click="TogglePassword">
                            <span class="input-group-text">
                          <i id="icon" class="fa fa-eye-slash"></i>
                        </span>
                        </div>
                    </div>
                </div>
                <div class="form-group">
                    <button class="btn btn-primary submit-btn btn-block">Login</button>
                </div>
            </div>
        </div>
    </form>
    <modal name="Error" @before-open="beforeOpen">
        <div class="p-4"><i class="fa fa-exclamation-circle text-danger"></i> {{ notice }}</div>
    </modal> 
</div>
</template>
<script>
import {store} from '../store/store'
import api from '../api'
import axios from 'axios';
import Cookies from 'js-cookie'
export default {
  name: 'Login',
  data() {
    return {
      Username: "",
      Password: "",
      notice:''
    };
  },
  store,
  methods: {
    beforeOpen (event) {
      this.notice = event.params;
    },
    TogglePassword () {
      var x = document.getElementById("password-field");
      if (x.type === "password") {
        x.type = "text";
        document.getElementById("icon").className = 'fa fa-eye';
      } else {
        x.type = "password";
        document.getElementById("icon").className = 'fa fa-eye-slash';
      }
    },
    Login: function () {
      if (this.Username == '' || this.Password == '') {
          this.$toast.open({
              message: 'Please enter username and password',
              type: 'error',
              position: 'top-right',
              dismissible: true   
          })    
      } else {
        axios.post(api.baseUrl + api.login, {
          "userName": this.Username,
          "passwordHash": this.Password
          /* eslint-disable no-unused-vars */
        }).then(res => 
          {
            Cookies.set('role', res.data.role, { expires: 0.25 });
            Cookies.set('token', res.data.token , { expires: 0.25 });
            Cookies.set('userName', res.data.username, { expires: 0.25 });
            Cookies.set('fullName', res.data.fullname, { expires: 0.25 });
            Cookies.set('id', res.data.userId, { expires: 0.25 });
            location.reload();
          })
          .catch(err => {
            this.$toast.open({
                message: 'Wrong password',
                type: 'error',
                position: 'top-right',
                dismissible: true   
            })            
          }
          );
      } 
    } 
  }
}
</script>

