<template>
<li class="timeline-item">
    <p class="timeline-content"><a><label class="col-sm-3">Logic Test</label></a></p>
    <p class="event-time">
    </p>
    <div class="card mt-3">
        <div class="card-body ">
            <div class="forms-sample">
                <div class="form-group row">
                    <label class="col-sm-3 col-form-label">Test Date</label>
                    <div class="col-sm-9">
                        <DateTimePicker :disabled="role=='Interviewer'" id='time_test' v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )).time"></DateTimePicker>
                    </div>
                </div>
                <div class="form-group row">
                    <label class="col-sm-3 col-form-label">Result</label>
                    <div class="col-sm-9">
                        <input :disabled="role=='Interviewer'" id='result_test' v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )).result" type="text" class="form-control" placeholder="">
                    </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3 col-form-label">Note</label>
                  <div class="col-sm-9">
                    <textarea :disabled="role=='Interviewer'" id="note_test" v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )).note" rows="2" class="form-control"></textarea>
                  </div>
                </div> 
                <div v-if="role!='Interviewer'">                               
                  <button @click="Update" type="submit" class="btn btn-success mr-2">Save</button>
                  <button class="btn disabled" >{{ status }}</button>
                  <button v-for="(status) in getNextStatus" :key="status.index" class="btn btn-outline-primary mr-2" @click="setStatus(status.name)">{{ status.name }}</button>
                </div>
            </div>
        </div>
    </div>
</li>
</template>
<script>
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css'    
import DateTimePicker from 'vue-bootstrap-datetimepicker';
import axios from 'axios';
import Cookies from 'js-cookie'
import {store} from '../../../store/store'
import api from '../../../api'
export default {
  name: 'LogicalTest',
  store,
  data() {
    return {
      TestDate: '',
      curentStatus: (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )).status,
      statusList: this.$store.getters.RoundStatus,
      status: (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )).status,
      result: '',
      note: '',
      role: Cookies.get('role')
    }
  },
  components: {
    DateTimePicker
  },
  computed: {
    getNextStatus: function () {
      function FilterStatus(Item) {
        return Item.statusOf == 'Test';
      }    
      let getTestStatus = this.statusList.filter(FilterStatus)  
      let arrayGetStatus = [];
      let idCurrent = null;
      getTestStatus.forEach(Item => {
        if (!idCurrent) {
          if (Item.name == this.curentStatus) idCurrent = Item.index
        } else {
          if (Item.index == idCurrent + 1 || Item.index == -idCurrent) {
            arrayGetStatus.push(Item);
          }
        }
      });
      return arrayGetStatus
    }

  },
  created: function () {

  },
  methods: {
    setStatus: function (text) {
      this.status = text
      this.Update()
    },
    Update: function () {    
      let date = Date.parse(document.getElementById("time_test").value) + 7*60*60*1000 + 7*60*1000
      let Data =  {
        "id": (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Test' )).id,
        "name": "Test",
        "time": new Date((new Date(date)).toISOString()).toISOString(),
        "cvId": this.$store.getters.idEditCv,
        "status": this.status,
        "result": document.getElementById('result_test').value,
        "note": document.getElementById('note_test').value
      }
      axios.put(api.baseUrl + api.UpdateRound, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => { 
        this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv);
        this.$toast.open({
            message: "Update Successfull",
            type: 'success',
            position: 'top-right',
            dismissible: true   
        })         
        // eslint-disable-next-line no-console
        console.log(res);
      })/* eslint-disable no-unused-vars */
        .catch(err => {
        this.$toast.open({
            message: "Can't update with your Account",
            type: 'success',
            position: 'top-right',
            dismissible: true   
        })  
        });
      let getTestStatus = this.status 
      if (getTestStatus == 'Pass' && !this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )) {
        let Data = {
          "name": 'Interview',
          "cvId": this.$store.getters.idEditCv,
          "status": 'Not Invite Yet'
        }
        axios.post(api.baseUrl + api.AddRound, 
          Data,
          {
          headers: {
              "Authorization" : "Bearer " + Cookies.get('token')
          }
        })
        .then(res => {
          // eslint-disable-next-line no-console
          console.log(res);         
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })/* eslint-disable no-unused-vars */
          .catch(err => {
            this.$toast.open({
                message: "Can't update with your Account",
                type: 'danger',
                position: 'top-right',
                dismissible: true   
            })             
          });  
      }
      if (getTestStatus == 'Rejected' || getTestStatus == 'Not Come' || getTestStatus == 'Not Pass') {
        Data = {
          "id": this.$store.getters.idEditCv,
          "status": "Archived"
        }
        axios.put(api.baseUrl + api.CvUpdateStatus, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }/* eslint-disable no-unused-vars */
        }).then(res => {
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })/* eslint-disable no-unused-vars */.catch(err => {
          this.$toast.open({
              message: "Can't update with your Account!",
              type: 'danger',
              position: 'top-right',
              dismissible: true   
          })            
        })        
      }
    }
  }
}
</script>

<style>
.timeline .timeline-item:nth-child(4n + 1):before {
    background: #e0e5f1!important;
}   
</style>
