<template>
<li class="timeline-item">
    <p class="timeline-content"><label class="col-sm-3">Offer</label></p>
    <p class="event-time">
    </p>
    <div class="card mt-3">
        <div class="card-body ">
            <div class="forms-sample">
                <div class="form-group row">
                    <label class="col-sm-3 col-form-label">Offer Date</label>
                    <div class="col-sm-9">
                        <DateTimePicker v-model="OfferDate" :config="{format: 'MM/DD/YYYY'}"></DateTimePicker>
                    </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3 col-form-label" for="">Salary Offer</label>
                  <div class="col-sm-9">
                    <money v-model="OfferSalary" v-bind="money" class="form-control"></money>
                  </div>
                </div>                
                <div class="form-group row">
                  <label class="col-sm-3 col-form-label">Note</label>
                  <div class="col-sm-9">
                    <textarea v-model="Note" rows="2" class="form-control"></textarea>
                  </div>
                </div>                              
                <button @click="Update" class="btn btn-success mr-2">Save</button>
                <button class="btn disabled" >{{ curentStatus }}</button>
                <button v-for="(status) in getNextStatus" :key="status.index" class="btn btn-outline-primary mr-2" @click="setStatus(status.name)">{{ status.name }}</button>                
            </div>
        </div>
    </div>
    <p></p>
</li>
</template>
<script>
import DateTimePicker from 'vue-bootstrap-datetimepicker';
import {store} from '../../../store/store'   
import Cookies from 'js-cookie'
import api from '../../../api'
import axios from 'axios';
import {Money} from 'v-money'  
export default {
  name: 'GoToRound',
  store,
  data() {
    return {   
      OfferInfo: {},  
      OfferDate: null,
      OfferSalary: 0,
      statusList: this.$store.getters.RoundStatus,
      curentStatus: null,  
      Note: null,
      money: {
          decimal: ',',
          thousands: '.',
          prefix: '',
          suffix: ' đ',
          precision: 0,
          masked: false
        }      
    }
  },
  components: {
    DateTimePicker,
    Money
  },
  created: function (){
    this.OfferInfo = this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Offer' )
    this.$set(this, 'OfferSalary', this.$store.getters.DetailCvObj.salaryOffer)
    this.$set(this, 'OfferDate', this.OfferInfo.time)
    this.$set(this, 'curentStatus', this.OfferInfo.status)
    this.$set(this, 'Note', this.OfferInfo.note)
  },
  computed: {
    getNextStatus: function () {
      function FilterStatus(Item) {
        return Item.statusOf == 'Offer';
      }    
      let getTestStatus = this.statusList.filter(FilterStatus)  
      let arrayGetStatus = [];
      let idCurrent = null;
      getTestStatus.forEach(Item => {
        if (!idCurrent) {
          if (Item.name == this.curentStatus) idCurrent = Item.index
        } else {
          if (Item.index == idCurrent + 1 || Item.index == -idCurrent) {
            arrayGetStatus.push(Item);
          }
        }
      });
      return arrayGetStatus
    }
  },
  methods: {
    setStatus: function (text) {
      this.curentStatus = text
      this.Update()
    },    
    Update: function () {
      let date = Date.parse(this.OfferDate) + 7*60*60*1000
      let Data = {
        "id": this.OfferInfo.id,
        "time": new Date((new Date(date)).toISOString()).toISOString(),
        "name": "Offer",
        "cvId": this.$store.getters.idEditCv,
        "status": this.curentStatus,
        "note": this.Note
      }
      // eslint-disable-next-line no-console      
      axios.put(api.baseUrl + api.UpdateRound, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => {
        Data = {
          "salaryOffer": this.OfferSalary,
          "id": this.$store.getters.DetailCvObj.id
        }
        axios.put(api.baseUrl + api.cvUpdate, 
          Data,
          {
          headers: {
              "Authorization" : "Bearer " + Cookies.get('token')
          }
        })/* eslint-disable no-unused-vars */.then(res => {
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
          this.$toast.open({
              message: "Update Successfull",
              type: 'success',
              position: 'top-right',
              dismissible: true   
          })           
        })      
         
        // eslint-disable-next-line no-console
        console.log(res);        
      })/* eslint-disable no-unused-vars */.catch(err => this.$modal.show('Alert', {note: "Can't update with your Account!", class: 'fa fa-close text-danger'})); 
      if (this.curentStatus == 'Hired') {
        Data = {
          "id": this.$store.getters.idEditCv,
          "status": "Hired"
        }
        axios.put(api.baseUrl + api.CvUpdateStatus, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }/* eslint-disable no-unused-vars */
        }).then(res => {
          
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })         
      } 
      if (this.curentStatus == 'Rejected' || this.curentStatus == 'Not Come') {
        let IdCv = this.$store.getters.idEditCv
        Data = {
          "id": IdCv,
          "status": "Archived"
        }
        axios.put(api.baseUrl + api.CvUpdateStatus, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
        }).then(res => {
          this.$store.dispatch('GET_CV_DETAIL', IdCv)
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })        
      }               
    }    
  }
}
</script>

<style>
.timeline .timeline-item:nth-child(4n + 3):before {
    background: #4060a4!important;
}   
</style>
