<template>
<div class="col-12 row">  
  <div class="col-12 mb-3" v-if="role!='Interviewer'">
    <button @click="openModal('AddCvModal')" type="button" class="btn btn-success">
      + Add New CV
    </button> 
  </div>
  <div class="dropdown col-3 col-md-2">
    <div class="form-group">
      <label for="exampleFormControlSelect1">Level</label>
      <select v-model="level" class="form-control form-control-lg" v-on:change="Submit">
        <option>All</option>
        <option v-for="(level) in (this.$store.getters.Level)" :key="level.id">{{ level['name'] }}</option>
      </select> 
    </div>
  </div> 
  <div class="dropdown col-3 col-md-2">
    <div class="form-group">
      <label for="exampleFormControlSelect1">Position <button v-if="role!='Interviewer'" @click="openModal('TablePosition')" style="margin-top: -20px!important">+</button></label>
      <select v-model="position" class="form-control form-control-lg" v-on:change="Submit">
        <option>All</option>
        <option v-for="(position) in (this.$store.getters.Position)" :key="position.id">{{ position['name'] }}</option>
      </select>
    </div>
  </div>                                    
  <div class="ml-auto col-3">
    <form class="ml-auto search-form d-none d-md-block" v-on:submit.prevent="Submit">
      <div class="form-group">
        <label for="exampleFormControlSelect1">Search</label>
        <div class="input-group col-xs-12">
          <input v-model='keyword' type="search" class="form-control file-upload-info" placeholder="Search name">
          <span class="input-group-append">
            <button @click="Submit()" class="file-upload-browse btn btn-info" type="button"><i class="fa fa-search"></i></button>
          </span>
        </div>
      </div>
    </form>
  </div>
  <div class="col-12">
    <div class="btn-group float-right d-inline" role="group" aria-label="group" style="border: none">
      <paginate
        :page-count=this.$store.getters.NumberPageCv
        :page-range="3"
        :margin-pages="2"
        :click-handler="clickCallback"
        :prev-text="'Prev'"
        :next-text="'Next'"
        :container-class="'pagination'"
        :page-class="'page-item'">
      </paginate>
    </div>    
    <div class="dropdown float-left d-inline">
      <div class="form-group">
        <select v-model="number" class="form-control form-control-lg" @change="Submit">
          <option>5</option>
          <option>10</option>
          <option>20</option>
          <option>50</option>
          <option>100</option>
        </select>
      </div>
    </div>     
  </div>
  <modal name="TablePosition">
    <Position/>
  </modal>    
  <modal name="AddCvModal">
    <AddCvModal/>
  </modal>  
</div>  
</template>
<script>
import AddCvModal from '../modal/CvModal.vue';  
import Position from '../modal/Position.vue';
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
import Paginate from 'vuejs-paginate'
import {store} from '../../store/store'  
export default {
  name: 'CvFilter',
  data() {
    return {
      level: 'All',
      position: 'All',
      keyword: '',
      number: 10,
      currentPage: 1,
      allPage: 1,
      allResult: 0,
      role: Cookies.get('role')
    }
  },
  store,
  components: {
    AddCvModal,
    Paginate,
    Position
  },
  created: function()
  {
    if (!this.$store.getters.Level){
      axios.get(api.baseUrl + api.getLevel, {
        headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_LEVEL', res.data))
        .catch(err => alert (err));
    }  
    if (!this.$store.getters.Position){
      axios.get(api.baseUrl + api.getPosition, {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => this.$store.commit('CHANGE_POSITION', res.data))
        .catch(err => alert (err));
    }    
  },
  methods: {
    clickCallback (pageNum) {
      this.currentPage = pageNum
      this.Submit()
    },
    openTab: function (tab) {
      this.$store.commit('CHANGE_TAB', tab)  
    },
    openModal (NameModal) {
      this.$modal.show(NameModal);
    },
    closeModal () {
      this.$modal.hide('AddCvModal');
    },
    Submit () {
      let lId, pId;
      if(this.level == 'All') {
        lId = 0
      } else {
        lId = (this.$store.getters.Level).find(Obj => Obj.name === this.level);
        lId = lId.id
      }  
      if(this.position == 'All') {
        pId = 0
      } else {
        pId = (this.$store.getters.Position).find(Obj => Obj.name === this.position); 
        pId = pId.id
      }      
      let Data = {
        "levelId": lId,
        "positionId": pId,
        "keyword": this.keyword,
        "currentPage": this.currentPage,
        "pageSize": parseInt(this.number)
      }
      axios.post(api.baseUrl + api.cvList, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
        /* eslint-disable no-unused-vars */
      }).then(res => {
        this.$store.commit('CHANGE_CV_LIST', res.data.pageData);
        this.$store.commit('CHANGE_CV_PAGE', res.data.totalPage)
        this.currentPage = 1
      })
        .catch(err => {
          if (err) {
            Cookies.remove('token')
            location.reload();
          }
        });     
    },
    GoToPage (page) {
      this.currentPage = page
    }
  }
}
</script>

