<template>
<li class="timeline-item">
    <p class="timeline-content"><label class="col-sm-3">Interview</label></p>
    <p class="event-time">
    </p>
    <div class="card mt-3">
        <div class="card-body ">
            <div class="forms-sample">
                <div class="form-group row">
                    <label class="col-sm-3 col-form-label">Test Date</label>
                    <div class="col-sm-9">
                        <DateTimePicker :disabled="role=='Interviewer'" id='time_interview' v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).time"></DateTimePicker>
                    </div>
                </div>
                <div class="form-group row">
                    <label class="col-sm-3 col-form-label">Interviewers</label>
                      <div class="col-sm-9 container-checkbox">
                      <multiselect :disabled="role=='Interviewer'" v-model="internalSelectedInterviewers" :options="this.$store.getters.InterviewerList" :multiple="true" :close-on-select="false" :clear-on-select="true" :preserve-search="true" placeholder="" label="fullName" track-by="fullName" :preselect-first="false">
                        <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect_single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }} interviewers selected</span></template>
                      </multiselect>                       
                    </div>
                </div>
                <div class="form-group row">
                  <label class="col-sm-3 col-form-label">Feedback of Interviewers</label>
                  <div class="col-sm-9">
                    <textarea v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).feedBackLink" id="feedback_interviewer" rows="2" class="form-control"></textarea>
                  </div>
                </div>                                
                <div class="form-group row">
                  <label class="col-sm-3 col-form-label">Feedback of BOD</label>
                  <div class="col-sm-9">
                    <textarea :disabled="role=='Interviewer'" v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).noteOfBOD" id="feedback_bod" rows="2" class="form-control"></textarea>
                  </div>
                </div> 
                <div class="form-group row">
                  <label class="col-sm-3 col-form-label">Note</label>
                  <div class="col-sm-9">
                    <textarea v-model="(this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).note" id="note_interview" rows="2" class="form-control"></textarea>
                  </div>
                </div>                 
                <button @click="Update" class="btn btn-success mr-2 d-inline">Save</button>
                <div v-if="role!='Interviewer'" class="d-inline">
                  <button class="btn disabled" >{{ status }}</button>
                  <button v-for="(status) in getNextStatus" :key="status.index" class="btn btn-outline-primary mr-2" @click="setStatus(status.name)">{{ status.name }}</button>
                </div>
            </div>
        </div>
    </div>
    <p></p>
</li>
</template>
<script>
import DateTimePicker from 'vue-bootstrap-datetimepicker';
import {store} from '../../../store/store'   
import multiselect from 'vue-multiselect'
import Cookies from 'js-cookie'
import api from '../../../api'
import axios from 'axios';
//this.$set(this, 'internalSelectedInterviewers', val)
export default {
  name: 'GoToRound',
  store,
  data() {
    return {
      InterviewDate:  '',
      internalSelectedInterviewers: null,
      statusList: this.$store.getters.RoundStatus,
      curentStatus: (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).status,
      status: (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).status,
      role: Cookies.get('role')   
    }
  },
  components: {
    DateTimePicker,
    multiselect
  },
  methods: {
    setStatus: function (text) {
      this.status = text
      this.Update()
    },    
    Update: function () {
      let arr = []
      let arrData = this.internalSelectedInterviewers
      arrData.forEach(Item => {
        arr.push({ "fullName": Item.fullName, "id": Item.id })
      })
      let date = Date.parse(document.getElementById("time_interview").value) + 7*60*60*1000
      let Data = {
        "id": (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).id,
        "time": new Date((new Date(date)).toISOString()).toISOString(),
        "interviewers": arr,
        "name": "Interview",
        "cvId": this.$store.getters.idEditCv,
        "status": this.status,
        "feedBackLink": document.getElementById('feedback_interviewer').value,
        "noteOfBOD": document.getElementById('feedback_bod').value,
        'note': document.getElementById('note_interview').value
      }
      axios.put(api.baseUrl + api.UpdateRound, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => {
        this.$toast.open({
            message: "Update Successfull",
            type: 'success',
            position: 'top-right',
            dismissible: true   
        })          
        this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        
        // eslint-disable-next-line no-console
        console.log(res);        
      })/* eslint-disable no-unused-vars */.catch(err => 
        this.$toast.open({
            message: "Can't update with your Account",
            type: 'error',
            position: 'top-right',
            dismissible: true   
        })        
      );
      let getTestStatus = this.status
      if (getTestStatus == 'Pass' && !this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Offer' )) {
        let Data = {
          "name": 'Offer',
          "cvId": this.$store.getters.idEditCv,
          "status": 'Not Offer Yet'
        }
        axios.post(api.baseUrl + api.AddRound, 
          Data,
          {
          headers: {
              "Authorization" : "Bearer " + Cookies.get('token')
          }
        })
        .then(res => {
          // eslint-disable-next-line no-console
          console.log(res);         
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })/* eslint-disable no-unused-vars */
          .catch(err => 
        this.$toast.open({
            message: "Can't update with your Account",
            type: 'danger',
            position: 'top-right',
            dismissible: true   
        })
            );  
      } 
      if (getTestStatus == 'Rejected' || getTestStatus == 'Not Come' || getTestStatus == 'Not Pass') {
        Data = {
          "id": this.$store.getters.idEditCv,
          "status": "Archived"
        }
        axios.put(api.baseUrl + api.CvUpdateStatus, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }/* eslint-disable no-unused-vars */
        }).then(res => {
          
          this.$store.dispatch('GET_CV_ROUND', this.$store.getters.idEditCv)
        })        
      }                
    }
  },
  created: function () {
    this.$set(this, 'internalSelectedInterviewers', (this.$store.getters.RoundOfCv.find( ({ name }) => name === 'Interview' )).interviewers)
    axios.get(api.baseUrl + api.GetInterviewers, 
      {
      headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
      }
    }).then(res => this.$store.commit('GET_INTERVIEWERS', res.data))   
  },
  computed: {
    getNextStatus: function () {
      function FilterStatus(Item) {
        return Item.statusOf == 'Interview';
      }    
      let getTestStatus = this.statusList.filter(FilterStatus)  
      let arrayGetStatus = [];
      let idCurrent = null;
      getTestStatus.forEach(Item => {
        if (!idCurrent) {
          if (Item.name == this.curentStatus) idCurrent = Item.index
        } else {
          if (Item.index == idCurrent + 1 || Item.index == -idCurrent) {
            arrayGetStatus.push(Item);
          }
        }
      });
      return arrayGetStatus
    }
  }
}
</script>

<style>
.multiselect-active {
  height: 400px!important;
}
.multiselect_single{
  font-family: "roboto", sans-serif;
  font-size: 0.75rem;
  color: #212529;
  padding: 0 .25rem;
  line-height: 14px;
  font-weight: 300;  
}
.multiselect__tag{
  background-color: #4060a4!important
}
.multiselect__option--selected {
  display: none!important
}
.multiselect__option--highlight {
  background-color: #a7abb5!important
}
.timeline .timeline-item:nth-child(4n + 2):before {
    background: #7ba5ff!important;
}   
@import "https://unpkg.com/vue-multiselect@2.1.0/dist/vue-multiselect.min.css";
</style>
