<template>
<div class="col-12 row"> 
  <div class="col-12 mb-3">
    <button @click="openModal" type="button" class="btn btn-success">
      + Add New Account
    </button> 
  </div> 
  <div class="dropdown col-3 col-md-2">
    <div class="form-group">
      <label for="exampleFormControlSelect1">Account Type</label>
      <select v-model="role" v-on:change="search" class="form-control form-control-lg" id="exampleFormControlSelect1">
        <option>All</option>
        <option>Admin</option>
        <option>Hr</option>
        <option>Interviewer</option>
      </select>
    </div>
  </div> 
  <div class="ml-auto col-3">
    <form class="ml-auto search-form d-none d-md-block" v-on:submit.prevent="search">
      <div class="form-group">
        <label for="exampleFormControlSelect1">Search</label>
        <div class="input-group col-xs-12">
          <input v-model="keyword" type="search" class="form-control file-upload-info" placeholder="Search name" >
          <span class="input-group-append">
            <button @click="search" class="file-upload-browse btn btn-info" type="button"><i class="fa fa-search"></i></button>
          </span>
        </div>
      </div>
    </form>
  </div>
  <modal name="AddAccountModal">
    <AddAccountModal @searchAccounts="search"/>
  </modal>
</div>  
</template>
<script>
import AddAccountModal from '../modal/AddAccountModal.vue';  
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
export default {
  name: 'AccountFilter',
  data() {
    return {
      role: 'All',
      keyword: ''
    }
  },
  components: {
    AddAccountModal
  },
  created: function()
  {
    
  },
  methods: {
    openModal () {
      this.$modal.show('AddAccountModal');
    },
    closeModal () {
      this.$modal.hide('AddAccountModal');
    },
    search () {
      let Data
      if (this.role == 'All') {
        Data = {
          "keyword": this.keyword,
          "currentPage": 1,
          "pageSize": 1000
        }        
      } else {
        Data = {
          "keyword": this.keyword,
          "role": this.role,
          "currentPage": 1,
          "pageSize": 1000
        }
      }
      axios.post(api.baseUrl + api.accountList, 
        Data,
        { headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
          }
        }).then(res => this.$store.commit('CHANGE_ACCOUNT_LIST', res.data))
      .catch(err => alert (err));
    }
  }
}
</script>

<style>
.btn-info {
    color: #fff;
    background-color: gray!important;
    border-color: gray!important;
}  
</style>
