<template>
  <div class="content-wrapper">
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="col-12">
          <div class="card-body col-12">
            <div class="pl-0 col-6">
              <form class="ml-auto search-form d-none d-md-block" v-on:submit.prevent="Add">
                <div class="form-group">
                  <label for="exampleFormControlSelect1">Add new position</label>
                  <div class="input-group col-xs-12">
                    <input v-model='ValueAdd' type="search" class="form-control file-upload-info" placeholder="">
                    <span class="input-group-append">
                      <button @click="Add" class="file-upload-browse btn btn-success" type="button"><i class="fa fa-plus"></i></button>
                    </span>
                  </div>
                </div>
              </form>
            </div>            
            <div class="row mt-4" style="max-height: 300px;overflow-y: auto">
              <table class="table mt-3 col-12">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Position</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(position, index) in PositionList" :key="position.id">
                    <td>{{ index + 1 }}</td>
                    <td class="position-relative">
                      <div>{{ position.name }}</div>
                      <form v-on:submit.prevent="Save">
                        <input class="form-group position-absolute" style="top: 8px" type="text"  v-model="ValueEdit" v-if="EditId == position.id">
                      </form>  
                    </td>
                    <td>
                      <a @click="Edit(position.id, position.name)"><i class="text-primary fa fa-edit"></i></a>
                      <a v-confirm="{
                          loader: true,
                          message: 'Do you sure want delete?'}"
                          @click="Delete(position.id)">
                          <i class="ml-1 text-danger fa fa-close"></i></a>
                    </td> 
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {store} from '../../store/store'
import axios from 'axios';
import api from '../../api'
import Cookies from 'js-cookie'
export default {
  name: 'Position',
  data() {
    return {
      PositionList: null,
      EditId: null,
      ValueEdit: null,
      ValueAdd: null
    }
  },  
  store,
  created: function () {
    this.Load()
  },
  methods: {
    Load () {
      axios.get(api.baseUrl + api.getPosition, {
        headers: {
          "Authorization" : "Bearer " + Cookies.get('token')
        }
      }).then(res => {
        this.$set(this, 'PositionList', res.data)
        this.$store.commit('CHANGE_POSITION', res.data)
      }).catch(err => alert (err)); 
    },
    Add () {
      let Data = {
        "name": this.ValueAdd,
        "note": "",
      }
      axios.post(api.baseUrl + api.AddPosition,
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
        /* eslint-disable no-unused-vars */
      }).then(res => {
        this.Load()
      }).catch(err => {
        this.Load()   
      })      
    },
    Edit (id, name) {
      this.$set(this, 'EditId', id)
      this.$set(this, 'ValueEdit', name)
    },
    Delete (id) {
      axios.delete(api.baseUrl + api.DeletePosition + id,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
        /* eslint-disable no-unused-vars */
      }).then(res => {
        this.$toast.open({
          message: "Delete successful",
          type: 'success',
          position: 'top-right',
          dismissible: true   
        })  
        this.Load()
      }).catch(err => {
        this.$toast.open({
            message: "Delete unsuccessful",
            type: 'error',
            position: 'top-right',
            dismissible: true   
        })          
      })
    },
    Save () {
      let Data = {
        "name": this.ValueEdit,
        "note": "",
        "id": this.EditId      
      }
      axios.put(api.baseUrl + api.UpdatePosition, 
        Data,
        {
        headers: {
            "Authorization" : "Bearer " + Cookies.get('token')
        }
        /* eslint-disable no-unused-vars */
      }).then(res => {
        this.$toast.open({
            message: "Save successful",
            type: 'success',
            position: 'top-right',
            dismissible: true   
        })  
        this.Load()
      }).catch(err => {
        this.$toast.open({
            message: "Save unsuccessful",
            type: 'error',
            position: 'top-right',
            dismissible: true   
        })     
        this.Load()     
      })      
      this.$set(this, 'EditId', null)

    }
  }
}
</script>

